* {
  font-family: "Fira Sans", sans-serif;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

nav {
  background-color: rgba(135, 207, 235, 0.315);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  transition: all 0.4s;
}
nav .container .logo {
  text-decoration: none;
  font-size: 3em;
  color: black;
  font-weight: bold;
  transition: all 0.4s;
}
nav .container ul {
  width: 60%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 0;
}
nav .container ul li a {
  text-decoration: none;
  color: black;
  font-size: 15px;
  font-weight: 800;
}
nav .container .burger {
  display: none;
  cursor: pointer;
}
nav .container .burger div {
  width: 30px;
  height: 3px;
  background-color: black;
  margin: 7px 0;
  transition: all 0.4s ease-in-out;
}

.container .nav-active {
  transform: translateX(0%);
}

.toggle .line1 {
  width: 30px;
  transform: rotate(45deg) translateY(14px);
  background-color: black;
}

.toggle .line2 {
  transform: translateX(100%);
  opacity: 0;
}

.toggle .line3 {
  width: 30px;
  transform: rotate(315deg) translateY(-14px);
}

@media (max-width: 600px) {
  nav {
    justify-content: space-between;
  }
  nav .container .logo {
    font-size: 24px;
  }
  nav .container ul {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgba(135, 207, 235, 0.288);
    backdrop-filter: blur(2.3px);
    -webkit-backdrop-filter: blur(2.3px);
    top: 10vh;
    right: 0px;
    width: 50%;
    height: 90vh;
    transform: translateX(100%);
    transition: all 0.5s ease-in-out;
  }
  nav .container ul li a {
    color: black;
  }
  nav .container .burger {
    display: block;
  }
}
.container {
  max-width: 1150px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 991.98px) {
  .container {
    max-width: 970px;
  }
}
@media (max-width: 767.98px) {
  .container {
    max-width: 740px;
  }
}
@media (max-width: 479.98px) {
  .container {
    max-width: none;
  }
}

.home {
  padding-top: 200px;
  padding-bottom: 100px;
}
@media (max-width: 767.98px) {
  .home {
    padding-top: 150px;
  }
}
.home .container {
  display: flex;
  align-items: center;
  gap: 50px;
}
@media (max-width: 991.98px) {
  .home .container {
    flex-wrap: wrap;
  }
}
.home .home_left {
  width: 600px;
}
@media (max-width: 991.98px) {
  .home .home_left {
    flex: 1 0 100%;
  }
}
.home .home_left h1 {
  font-weight: bold;
  font-size: 40px;
  color: black;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
@media (max-width: 479.98px) {
  .home .home_left h1 {
    font-size: 26px;
  }
}
.home .home_left h1 span {
  color: skyblue;
}
@media (max-width: 991.98px) {
  .home .home_left h1 {
    max-width: 100%;
    width: 100%;
  }
}
.home .home_left p {
  width: 600px;
  margin: 25px 0;
}
@media (max-width: 991.98px) {
  .home .home_left p {
    width: 100%;
  }
}
.home .home_left button {
  border: none;
  background-color: black;
  color: white;
  cursor: pointer;
  padding: 12px 25px;
  border-radius: 10px;
  transition: all 0.4s;
}
.home .home_left button:hover {
  box-shadow: 0 0 30px #2a9ac7;
}
.home .home_right img {
  max-width: 100%;
  width: 500px;
  height: auto;
  border-radius: 30px 0 30px 0;
}
@media (max-width: 991.98px) {
  .home .home_right img {
    max-width: 100%;
    width: 100%;
  }
}